<template>
    <div class="main">
        <div class="under_header px-2 pt-1 d-flex justify-content-between ">
            <div class="news_title">
                <h5>For you</h5>
            </div>
              <div v-if="!showWeaterTemp" class="fa-1x spiner pe-2">
                   <i class="fas fa-circle-notch fa-spin"></i>
                </div>
            <div v-else class="wearher d-flex">
                <div class="wearher_img px-1">
                    <img :src="showWeaterImg" alt="">
                </div>
                <div class="wearher_temp">
                    <strong>
                        {{showWeaterTemp}}°C
                    </strong>
                </div>
            </div>
        </div>
     
        <div class="lines pt-3 pb-3"><hr></div>
        <div 
            v-for="(news, i) in allNews" :key="i"
            class="news d-flex px-2 py-2">
                <div class="flex-shrink-0 img_news">
                    <img
                    v-show="news.urlToImage" 
                     :src="news.urlToImage" alt="">
                </div>
            <div class="content_news px-2 d-flex flex-column">
                <div class="contect_category ">
                    <a :href="news.url">
                        {{news.source.name}}
                    </a>
                </div>
                <div class="contect_title ">
                    <strong>{{news.title}}</strong>
                </div>
                <div class="content_time_ago">
                    <p>{{news.dateAgo}}</p>
                </div>
                
            </div>
        </div>   
        <nav>
            <div 
                :style="{'display':statusBar}"
                class="overlay"
                :class="{'slideOutRight':!clickNavBar,
                        'slideInLeft': clickNavBar}"
                    >
                <div class="d-flex flex-column align-items-center">
                    <div class="p-4 ">
                        <h1 class="fs-1">News</h1>
                    </div>
                    <div class="p-4">
                        <h1>Weather</h1>
                    </div>
                    <div class="p-4">
                        <h1>Price USD</h1>
                    </div>
                    <div class="p-4">
                        <h1>Politic</h1>
                    </div>
                    <div class="p-4">
                        <h1>World</h1>
                    </div>
                    
                </div>
                
            </div>
            <div 
            class="iconBar text-primary"
            @click="clickNavBar = !clickNavBar"
            >
            <i 
            class="fa-solid fa-xl"
            :class="{'fa-bars': !clickNavBar,
                    'fa-bars-staggered':clickNavBar,
                    'active':clickNavBar}">
            </i>
           </div>
           
            
        </nav>
        
    </div>
    
</template>

<script>

import {mapGetters,mapActions} from 'vuex'
export default {
    name:'NewsBlock',
    data(){
        return{
            clickNavBar: false,
            statusBar: 'none'
            
            
        }
    },
    
    computed:{
        ...mapGetters(['allNews','allWeather','showWeaterTemp','showWeaterImg']),
    },
    methods: {
        ...mapActions(['fetchNews','weatherFetch']),
       
    },
    watch:{
        clickNavBar(newV){
            if (newV) {
                return this.statusBar = 'block'
            }
            return ''
        }
    },
   
    mounted(){
        this.fetchNews();
        this.weatherFetch();
    }
    

    
}
</script>

<style scoped>
       
*{
    margin: 0;
    
}

.img_news img{
    width: 100px;
    height: 100px;
    object-fit:cover;
    border-radius: 10%;
}
.contect_title strong{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    line-height: 20px;
    max-height: 60px;
    
}
.content_time_ago{
    color: #B7B7B7;
}
.contect_category a{
    color: #eb3b3b;
    text-decoration: none;
}
.contect_category a span{ 
    font-size: 12px;
    color: #B7B7B7;
    text-decoration: none;
}
.wearher_img img {
    width: 24px;
    height: 25px;
    object-fit:cover;
}
.main{
    padding-top: 65px;
}
.iconBar{
    position: fixed;
    bottom: 0;
    right: 0;
    padding: 20px;
    margin: 10px;
    background: #B7B7B7;
    border: #B7B7B7;
    border-radius: 50%;
    transition: 0.3s ease-in-out;
    z-index: 3;
    
}

.active{
    color: #eb3b3b;
    transform: scale(1.12);
    transition: 0.2s ease-in-out;
    
    
}
.overlay {
  position: fixed; /* Sit on top of the page content */
  display: block; /* Hidden by default */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #e9e9e9; /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
  
}

.slideInLeft {
  -webkit-animation-name: slideInLeft;
  animation-name: slideInLeft;
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  }
  @-webkit-keyframes slideInLeft {
  0% {
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  visibility: visible;
  }
  100% {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  }
  }
  @keyframes slideInLeft {
  0% {
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  visibility: visible;
  }
  100% {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  }
  } 


   .slideOutRight {
  -webkit-animation-name: slideOutRight;
  animation-name: slideOutRight;
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  }
  @-webkit-keyframes slideOutRight {
  0% {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  }
  100% {
  visibility: hidden;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  }
  }
  @keyframes slideOutRight {
  0% {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  }
  100% {
  visibility: hidden;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  }
  } 


</style>